@media ( max-width:1200px )
  .header__link--logo-index
    transform: none
  .main-slider__head
    font-size: 85px
  .main-slider__slide::before
    left: -40px
  .page-hero__wrapper .slider-aside
    left: 0px
  .slider-aside__content
    // margin-left: 30px
  // .mini-slider__slider-wrapper
  //   width: 730px
  .page__work
    &:first-child::before
      left: -15px
    &:last-child::before
      right: -30px

@media ( max-width:1100px )
  .header__link--logo-index
    transform: none
  .main-slider__container
    margin-left: 40px
  .main-slider__head
    font-size: 80px
  .main-slider__btn-prev
    left: 7%
  .main-slider__btn-next
    right: 5%
  .slider-aside__content
    // margin-left: 15px
  .main-slider__slide::before
    left: -55px
  // .mini-slider__container
  //   justify-content: space-between
  .mini-slider__left
    width: 45%
    height: auto
  .mini-slider__slider
    width: 50%
