.slider-aside
  position: absolute
  display: flex
  align-items: flex-end
  bottom: 0
  left: 0
  // width: 321px
  // height: 800px
  z-index: 2
  &--contact
    top: 27px
    bottom: auto
    left: 15px
    // width: 30px
  &__content
    display: flex
    flex-direction: column
    align-items: center
    justify-content: flex-end
    height: 516px
    // margin-left: 71px
    // margin-bottom: 40px
    z-index: 3
    &--contact
      margin-left: 0
      margin-bottom: 0
      // transform: rotate(-90deg)
  &__divider
    width: 1px
    height: 75px
    background-color: $orange
  &__text
    width: 19px
    height: 275px
    margin-bottom: 25px
    span
      display: block
      // width: 333px
      font-weight: 300
      font-size: 12px
      line-height: 160%
      writing-mode: vertical-rl
      letter-spacing: 0.3em
      color: $grey
      transform: rotate(-180deg)
  &__list
    margin-top: 25px
    // margin-bottom: 0
