.main-slider
  position: relative
  height: 100vh
  min-height: 680px
  max-height: 800px
  background-color: $black
  color: $white
  &__container
    box-sizing: border-box
    display: flex
    // justify-content: flex-end
    flex-direction: column
    // justify-content: center
    align-items: flex-start
    width: 100%
    padding-top: 314px
    padding-left: 70px
    padding-right: 70px
  &__aside
    position: relative
    left: 15px
    & .slider-aside
      bottom: 67px
  &__slide
    display: flex
    // align-items: center
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    // &::before
    //   position: absolute
    //   content: ''
    //   top: 0
    //   left: -9px
    //   width: 321px
    //   height: 100%
    //   // background-image: url(../images/slider-aside.svg)
    //   background-size: cover
    //   background-position: 0 0
    //   z-index: 3
    // &::after
    //   position: absolute
    //   content: ''
    //   top: 0
    //   left: 0
    //   width: 100%
    //   height: 100%
    //   background-color: $black
    //   opacity: .6
    //   z-index: 1
  &__button
    // display: inline-block;
    width: 270px
    margin-top: 30px
    padding: 12px 46px 12px 40px
    font-size: 20px
    line-height: 110%
    text-transform: uppercase
    &::before
      right: 33px
  &__head
    position: relative
    max-width: 960px
    // width: 100%
    font-size: 100px
    line-height: 120%
    color: $white
    z-index: 5
    span
      color: $orange
  &__btn
    position: absolute
    top: 54%
    width: 25px
    height: 22px
    transform: translateY(-50%)
    cursor: pointer
    z-index: 4
    &-prev
      left: calc(50% - 492px)
    &-fill
      width: 25px
      height: 22px
      fill: $dark-grey
      object-fit: cover
      transition: fill 0.3s ease-in-out
    &-next
      right: calc(50% - 646px)
      transform: rotate(180deg)
